
.container{
    margin-top: 5%;
    border: 1px solid black;
    width: 70%;
    display: flex;
    justify-content: center;
    border-radius: 10px;
    
}

.header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 5px 5px 5px black;
}
.logo{
    width: 60px;
    transition: .2s;
}
.logo:hover{
    box-shadow: 3px 3px 3px 5px rgb(32, 129, 164);
    border-radius: 50%;
    cursor: pointer;
}
.logo:active{
    box-shadow: 3px 3px 3px 5px rgb(245, 6, 6);
}
.img{
    width: 100px;
}
.main__page{ 
    margin-top: 30px;
    width: 60%;
}
.page__menu{
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-evenly;
    /* border-bottom: 1px solid black; */
    transition: .2s;
}
.page__menu:hover{
    box-shadow: 3px 3px 3px 5px rgb(0, 0, 0);
    border-radius: 5px;
}

.menu__title{
    font-size: 40px;
}

.btn{
    padding: 20px;
    border-radius: 5px;
    background-color: black;
    color: white;
    font-size: 20px;
    transition: .2s;
}
.add__button:hover{
    box-shadow: 3px 3px 3px 5px rgb(32, 129, 164);
}
.add__button:active{
    padding: 25px;
    box-shadow: 3px 3px 3px 5px rgb(245, 6, 6);
}
.cart{
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-evenly;
}

/* Toggle */
.toggle {
    --width: 80px;
    --height: calc(var(--width) / 3);

    position: relative;
    display: inline-block;
    width: var(--width);
    height: var(--height);
    border-radius: var(--height);
    cursor: pointer;
  }

  .toggle input {
    display: none;
  }

  .toggle .slider {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: var(--height);
    border: 2px solid black;
    transition: all 0.4s ease-in-out;
  }

  .toggle .slider::before {
    content: '';
    position: absolute;
    top: 1px;
    left: 2px;
    width: calc(var(--height)*0.9);
    height: calc(var(--height)*0.9);
    border-radius: calc(var(--height) / 2);
    background-color: black;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
    transition: all 0.4s ease-in-out;
  }

  .toggle input:checked+.slider {
    border: 2px solid black;
  }

  .toggle input:checked+.slider::before {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .toggle .labels {
    position: absolute;
    top: 8px;
    left: 50px;
    width: 100%;
    height: 100%;
    color: black;
    font-size: 12px;
    font-family: sans-serif;
    transition: all 0.4s ease-in-out;
  }

  .toggle .labels::after {
    content: attr(data-off);
    position: absolute;
    opacity: 1;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
    transition: all 0.4s ease-in-out;
  }

  .toggle .labels::before {
    content: attr(data-on);
    position: absolute;
    opacity: 0;
    text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.4);
    transition: all 0.4s ease-in-out;
  }

  .toggle input:checked~.labels::after {
    opacity: 0;
  }

  .toggle input:checked~.labels::before {
    opacity: 1;
  }
